<template>
  <div class="analysis-container">
    <h2 class="section-title">üîç ËÆ§Áü•ÂàÜÊûê</h2>
    
    <!-- ÂéÜÂè≤ËÆ∞ÂΩïÈÄâÊã©Âô® -->
    <div class="history-selector mb-4">
      <div class="history-header">
        <label for="recordSelect">ÈÄâÊã©ËÆ∞ÂΩï:</label>
        <button class="btn btn-sm btn-outline-secondary" @click="showHistoryManager = true">
          <span class="btn-icon">üìã</span> ÁÆ°ÁêÜÂéÜÂè≤ËÆ∞ÂΩï
        </button>
      </div>
      <select id="recordSelect" class="form-select" v-model="selectedRecordIndex" @change="loadSelectedRecord">
        <option v-for="(record, index) in $store.state.thoughtRecords" :key="index" :value="index">
          {{ formatTimestamp(record.timestamp) }} - {{ truncateText(record.automaticThought, 30) }}
        </option>
      </select>
    </div>
    
    <!-- ÂéÜÂè≤ËÆ∞ÂΩïÁÆ°ÁêÜÂô®ÂºπÁ™ó -->
    <div class="history-manager" v-if="showHistoryManager">
      <div class="history-manager-content">
        <div class="history-manager-header">
          <h3>ÁÆ°ÁêÜÂéÜÂè≤ËÆ∞ÂΩï</h3>
          <button class="btn-close" @click="showHistoryManager = false">&times;</button>
        </div>
        <div class="history-manager-body">
          <div class="history-list">
            <div 
              v-for="(record, index) in $store.state.thoughtRecords" 
              :key="index"
              class="history-item"
            >
              <div class="history-item-content">
                <div class="history-item-date">{{ formatTimestamp(record.timestamp) }}</div>
                <div class="history-item-thought">{{ truncateText(record.automaticThought, 60) }}</div>
                <div class="history-item-status" :class="getRecordStatusClass(record)">
                  {{ getRecordStatus(record) }}
                </div>
              </div>
              <div class="history-item-actions">
                <button 
                  class="btn btn-sm btn-outline-danger" 
                  @click="deleteRecord(index)"
                  title="Âà†Èô§ËÆ∞ÂΩï"
                >
                  üóëÔ∏è
                </button>
              </div>
            </div>
            <div class="no-records" v-if="$store.state.thoughtRecords.length === 0">
              ÊöÇÊó†ÂéÜÂè≤ËÆ∞ÂΩï
            </div>
          </div>
        </div>
        <div class="history-manager-footer">
          <button class="btn btn-outline-secondary" @click="showHistoryManager = false">ÂÖ≥Èó≠</button>
        </div>
      </div>
    </div>
    
    <div class="card" v-if="currentRecord">
      <!-- Ëá™Âä®ÊÄùÁª¥Â±ïÁ§∫ -->
      <div class="analysis-section">
        <h5>üìå ‰Ω†ÁöÑËá™Âä®ÊÄùÁª¥</h5>
        <p class="thought-text">"{{ currentRecord.automaticThought }}"</p>
        
        <!-- Ê∑ªÂä†ÊÉÖÂ¢ÉÊèèËø∞ÊòæÁ§∫ -->
        <div class="situation-box" v-if="currentRecord.situation">
          <h6>üîç ÊÉÖÂ¢ÉÊèèËø∞</h6>
          <p class="situation-text">{{ currentRecord.situation }}</p>
        </div>
        
        <div class="meta-info">
          <span class="date-tag">{{ formatTimestamp(currentRecord.timestamp) }}</span>
          <div class="emotion-badges">
            <span v-for="(emotion, idx) in currentRecord.emotions" :key="idx" 
                  class="emotion-badge">{{ emotion }}</span>
          </div>
        </div>
      </div>

      <!-- ËÆ§Áü•ÂÅèÂ∑ÆÂàÜÊûê -->
      <div class="analysis-section">
        <h5>üß† ËØÜÂà´Âà∞ÁöÑËÆ§Áü•ÂÅèÂ∑Æ</h5>
        <div class="bias-tags" v-if="analysisResult.cognitiveBiases && analysisResult.cognitiveBiases.length > 0">
          <span 
            class="badge badge-pill"
            v-for="bias in analysisResult.cognitiveBiases"
            :key="bias.type"
            :style="{ backgroundColor: getBiasColor(bias.type) }"
            @click="selectBias(bias)"
          >
            {{ bias.type }} <span class="confidence">({{ bias.confidence }}%)</span>
          </span>
        </div>
        <div class="no-biases" v-else>
          <p v-if="analyzedOnce">Á≥ªÁªüÊú™Ê£ÄÊµãÂà∞ÊòéÊòæÁöÑËÆ§Áü•ÂÅèÂ∑ÆÔºå‰ΩÜËøôÂπ∂‰∏çÊÑèÂë≥ÁùÄÊÄùÁª¥ÂÆåÂÖ®Ê≤°ÊúâÈóÆÈ¢ò„ÄÇ
          ËØ∑ÁªßÁª≠ÊµèËßà‰∏ãÊñπÁöÑÂºïÂØºÊÄßÈóÆÈ¢òÔºåÊÄùËÄÉËá™Â∑±ÁöÑÊÉ≥Ê≥ïÊòØÂê¶Â≠òÂú®Ëøô‰∫õÊÉÖÂÜµÔºö</p>
          <ul v-if="analyzedOnce" class="bias-tips">
            <li>‰Ω†ÊòØÂê¶Âú®ËÄÉËôëÊúÄÁ≥üÁ≥ïÁöÑÂèØËÉΩÊÄßÔºü</li>
            <li>‰Ω†ÊòØÂê¶Âú®Áî®"ÈùûÈªëÂç≥ÁôΩ"ÁöÑÊñπÂºèÁúãÂæÖÊÉÖÂÜµÔºü</li>
            <li>‰Ω†ÊòØÂê¶‰ªéÂçï‰∏Ä‰∫ã‰ª∂ÂæóÂá∫‰∫ÜÊôÆÈÅçÊÄßÁªìËÆ∫Ôºü</li>
            <li>‰Ω†ÊòØÂê¶Áî®ÊÉÖÁª™ËÄåÈùû‰∫ãÂÆûÂú®ÂÅöÂà§Êñ≠Ôºü</li>
            <li>‰Ω†ÊòØÂê¶ÁªôËá™Â∑±Êàñ‰ªñ‰∫∫ËÆæÁΩÆ‰∫ÜËøáÈ´òÊ†áÂáÜÔºü</li>
          </ul>
          <p v-else>ÊöÇÊú™ËØÜÂà´Âà∞ËÆ§Áü•ÂÅèÂ∑ÆÔºåËØ∑ÁÇπÂáª"ÂºÄÂßãÂàÜÊûê"ËøõË°åÂàÜÊûê„ÄÇ</p>
        </div>
        <div class="bias-evidence" v-if="selectedBias">
          <div class="evidence-header">
            <h6>{{ selectedBias.type }} ÁöÑËØÜÂà´‰æùÊçÆÔºö</h6>
          </div>
          <p class="evidence-text">{{ selectedBias.evidence }}</p>
        </div>
      </div>

      <!-- ÂºïÂØºÊÄßÈóÆÈ¢ò -->
      <div class="analysis-section">
        <h5>üí° ÂºïÂØºÊÄßÈóÆÈ¢ò</h5>
        <ul class="question-list">
          <li v-for="(question, index) in analysisResult.guidingQuestions" :key="index">
            {{ question }}
          </li>
        </ul>
        <div class="no-questions" v-if="!analysisResult.guidingQuestions || analysisResult.guidingQuestions.length === 0">
          ÊöÇÊó†ÂºïÂØºÊÄßÈóÆÈ¢òÔºåËØ∑ÁÇπÂáª"ÂºÄÂßãÂàÜÊûê"ÁîüÊàêÈóÆÈ¢ò„ÄÇ
        </div>
      </div>

      <!-- Áî®Êà∑ÂõûÁ≠îËæìÂÖ• -->
      <div class="analysis-section">
        <h5>üìù ‰Ω†ÁöÑÊÄùËÄÉËÆ∞ÂΩï</h5>
        <textarea
          class="form-control"
          rows="4"
          v-model="userResponse"
          placeholder="ËØ∑ÂÜô‰∏ã‰Ω†ÂØπËøô‰∫õÈóÆÈ¢òÁöÑÊÄùËÄÉ..."
        ></textarea>
      </div>

      <!-- Êõø‰ª£ÊÉ≥Ê≥ïÁîüÊàê -->
      <div class="analysis-section" v-if="alternativeThought">
        <h5>üåà Âª∫ËÆÆÁöÑÊõø‰ª£ÊÉ≥Ê≥ï</h5>
        <p class="alternative-thought">"{{ alternativeThought }}"</p>
        <div class="feedback-buttons">
          <button 
            class="btn btn-success mr-2"
            @click="saveAlternativeThought"
          >
            üëç ‰øùÂ≠òËøô‰∏™ÊÉ≥Ê≥ï
          </button>
          <button 
            class="btn btn-outline-secondary"
            @click="generateAlternativeThought"
          >
            üîÑ ÈáçÊñ∞ÁîüÊàê
          </button>
        </div>
      </div>

      <!-- ÂàÜÊûêÊìç‰ΩúÊåâÈíÆ -->
      <div class="action-buttons" v-if="!isLoading">
        <button class="btn btn-primary" @click="analyzeThought">
          <span class="icon">üß†</span> ÂºÄÂßãÂàÜÊûê
        </button>
      </div>

      <!-- Âä†ËΩΩËøõÂ∫¶Êù° -->
      <div class="loading-container" v-if="isLoading">
        <div class="progress">
          <div class="progress-bar" 
               :style="{ width: `${loadingProgress}%` }" 
               :class="{'progress-bar-animated': isLoading}">
            {{ loadingStage }}
          </div>
        </div>
        <p class="loading-text">{{ loadingStage }} ({{ loadingProgress }}%)</p>
        <p class="loading-tips" v-if="loadingTip">{{ loadingTip }}</p>
      </div>
      
      <!-- ÊòæÁ§∫APIÈîôËØØ‰ø°ÊÅØ -->
      <div class="api-error alert alert-danger" v-if="apiError">
        <p><strong>ÂàÜÊûêËøáÁ®ã‰∏≠ÂèëÁîüÈîôËØØ</strong></p>
        <p>{{ apiError }}</p>
        <p class="mt-2">
          <button class="btn btn-sm btn-outline-danger" @click="apiError = null">ÂÖ≥Èó≠</button>
          <router-link to="/config" class="btn btn-sm btn-primary ml-2">Ê£ÄÊü•APIËÆæÁΩÆ</router-link>
        </p>
      </div>
    </div>

    <div class="alert alert-warning" v-else>
      ‚ö†Ô∏è ËØ∑ÂÖàÂÆåÊàêËá≥Â∞ë‰∏ÄÊù°ÊÄùÊÉ≥ËÆ∞ÂΩï
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      selectedRecordIndex: 0,
      currentRecord: null,
      analysisResult: {
        cognitiveBiases: [],
        guidingQuestions: []
      },
      userResponse: '',
      alternativeThought: '',
      isLoading: false,
      loadingProgress: 0,
      loadingStage: 'ÂáÜÂ§áÂàÜÊûê',
      loadingTip: '',
      apiError: null,
      showHistoryManager: false,
      selectedBias: null,
      analyzedOnce: false,
      loadingTips: [
        'ËÆ§Áü•ÂÅèÂ∑ÆÊòØÊàë‰ª¨ÊÄùÁª¥‰∏≠ÁöÑÁõ≤ÁÇπÔºåËØÜÂà´ÂÆÉ‰ª¨ÊòØÊ≤ªÁñóÁöÑÁ¨¨‰∏ÄÊ≠•...',
        'ËãèÊ†ºÊãâÂ∫ïÂºèÊèêÈóÆÂ∏ÆÂä©Êàë‰ª¨ÊåëÊàòËá™Â∑±ÁöÑÊÉ≥Ê≥ïÔºå‰øÉËøõÊõ¥Ê∑±ÂÖ•ÁöÑÊÄùËÄÉ...',
        'Êõø‰ª£ÊÄßÊÄùÁª¥‰∏çÊòØÁÆÄÂçïÂú∞ÁßØÊûÅÊÄùËÄÉÔºåËÄåÊòØÂØªÊâæÊõ¥Âπ≥Ë°°ÁöÑËßÜËßí...',
        'ËÆ§Áü•Ë°å‰∏∫ÁñóÊ≥ïÂº∫Ë∞ÉÊÄùÁª¥„ÄÅÊÉÖÁª™ÂíåË°å‰∏∫‰πãÈó¥ÁöÑËÅîÁ≥ª...',
        'ÊîπÂèòÊÉ≥Ê≥ï‰∏çÊòØ‰∏ÄËπ¥ËÄåÂ∞±ÁöÑÔºåÈúÄË¶ÅÊåÅÁª≠ÁöÑÁªÉ‰π†ÂíåËÄêÂøÉ...'
      ]
    };
  },
  computed: {
    apiConfig() {
      return this.$store.state.apiConfig;
    }
  },
  mounted() {
    // Ê£ÄÊü•ÊòØÂê¶Êúâ‰ªéstore‰º†ÈÄíËøáÊù•ÁöÑËÆ∞ÂΩïÁ¥¢Âºï
    if (this.$store.selectedRecordIndex !== undefined) {
      this.selectedRecordIndex = this.$store.selectedRecordIndex;
      // ‰ΩøÁî®ÂÆåÂêéÊ∏ÖÈô§ÔºåÈÅøÂÖçÂΩ±ÂìçÂêéÁª≠Êìç‰Ωú
      this.$store.selectedRecordIndex = undefined;
    } else if (this.$store.state.thoughtRecords.length > 0) {
      this.selectedRecordIndex = 0;
    }
    
    this.loadSelectedRecord();
  },
  methods: {
    formatTimestamp(timestamp) {
      if (!timestamp) return 'Êú™Áü•Êó∂Èó¥';
      return new Date(timestamp).toLocaleString('zh-CN', {
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    },
    
    truncateText(text, maxLength) {
      if (!text) return '';
      return text.length > maxLength ? text.substring(0, maxLength) + '...' : text;
    },
    
    loadSelectedRecord() {
      if (this.$store.state.thoughtRecords.length === 0) {
        this.currentRecord = null;
        return;
      }
      
      const record = this.$store.state.thoughtRecords[this.selectedRecordIndex];
      this.currentRecord = record;
      
      // Â¶ÇÊûúËÆ∞ÂΩï‰∏≠Â∑≤ÁªèÊúâÂàÜÊûêÁªìÊûúÂíåÊõø‰ª£ÊÉ≥Ê≥ïÔºåÂàôÂä†ËΩΩÂÆÉ‰ª¨
      if (record.analysisResult) {
        this.analysisResult = record.analysisResult;
      } else {
        this.analysisResult = {
          cognitiveBiases: [],
          guidingQuestions: []
        };
      }
      
      if (record.userResponse) {
        this.userResponse = record.userResponse;
      } else {
        this.userResponse = '';
      }
      
      this.alternativeThought = record.alternativeThought || '';
      this.selectedBias = null;
    },
    
    getBiasColor(biasType) {
      const colors = {
        'ÁÅæÈöæÂåñ': '#ff6b6b',
        'ÈùûÈªëÂç≥ÁôΩ': '#4ecdc4',
        'ËøáÂ∫¶Ê¶ÇÊã¨': '#45b7d1',
        'ÊÉÖÁª™Êé®ÁêÜ': '#96ceb4',
        'Â∫îËØ•ÈôàËø∞': '#ffeead',
        'ÂøÉÁêÜËøáÊª§': '#ffb347',
        '‰∏™‰∫∫Âåñ': '#c06c84',
        'ÊéßÂà∂Ë∞¨ËØØ': '#7579e7',
        'Ë¥¥Ê†áÁ≠æ': '#84b1ed',
        'ËØªÂøÉÊúØ': '#d183c9'
      };
      return colors[biasType] || '#6c757d';
    },
    
    selectBias(bias) {
      if (this.selectedBias === bias) {
        this.selectedBias = null;
      } else {
        this.selectedBias = bias;
      }
    },
    
    getRecordStatus(record) {
      if (record.alternativeThought) {
        return 'Â∑≤ÂÆåÊàêÂàÜÊûê';
      } else if (record.analysisResult && record.analysisResult.cognitiveBiases) {
        return 'ÂàÜÊûêËøõË°å‰∏≠';
      } else {
        return 'ÂæÖÂàÜÊûê';
      }
    },
    
    getRecordStatusClass(record) {
      if (record.alternativeThought) {
        return 'status-completed';
      } else if (record.analysisResult && record.analysisResult.cognitiveBiases) {
        return 'status-progress';
      } else {
        return 'status-pending';
      }
    },
    
    deleteRecord(index) {
      if (confirm('Á°ÆÂÆöË¶ÅÂà†Èô§ËøôÊù°ËÆ∞ÂΩïÂêóÔºüÂà†Èô§ÂêéÊó†Ê≥ïÊÅ¢Â§ç„ÄÇ')) {
        this.$store.state.thoughtRecords.splice(index, 1);
        this.$store.saveState();
        
        // Â¶ÇÊûúÂà†Èô§ÁöÑÊòØÂΩìÂâçÈÄâ‰∏≠ÁöÑËÆ∞ÂΩïÔºåÈáçÊñ∞Âä†ËΩΩ
        if (this.selectedRecordIndex === index) {
          this.selectedRecordIndex = 0;
          this.loadSelectedRecord();
        } 
        // Â¶ÇÊûúÂà†Èô§ÁöÑËÆ∞ÂΩïÁ¥¢ÂºïÂ∞è‰∫éÂΩìÂâçÈÄâ‰∏≠ÁöÑÁ¥¢ÂºïÔºåÈúÄË¶ÅË∞ÉÊï¥ÈÄâ‰∏≠Á¥¢Âºï
        else if (this.selectedRecordIndex > index) {
          this.selectedRecordIndex--;
        }
      }
    },

    async analyzeThought() {
      this.isLoading = true;
      this.loadingProgress = 0;
      this.loadingStage = 'ÂàùÂßãÂåñÂàÜÊûê';
      this.apiError = null;
      this.loadingTip = this.getRandomTip();
      
      try {
        // Á¨¨‰∏ÄÊ≠•ÔºöËØÜÂà´ËÆ§Áü•ÂÅèÂ∑Æ
        this.loadingStage = 'ËØÜÂà´ËÆ§Áü•ÂÅèÂ∑Æ';
        this.loadingProgress = 25;
        this.loadingTip = this.getRandomTip();
        const biasResponse = await this.callLLM(this.getBiasPrompt());
        this.analysisResult.cognitiveBiases = this.parseBiasResponse(biasResponse);
        this.analyzedOnce = true;

        // Á¨¨‰∫åÊ≠•ÔºöÁîüÊàêÂºïÂØºÈóÆÈ¢ò
        this.loadingStage = 'ÁîüÊàêÂºïÂØºÈóÆÈ¢ò';
        this.loadingProgress = 50;
        this.loadingTip = this.getRandomTip();
        const questionsResponse = await this.callLLM(this.getQuestionPrompt());
        this.analysisResult.guidingQuestions = this.parseQuestionResponse(questionsResponse);

        // ‰øùÂ≠òÁî®Êà∑ÂõûÂ∫î
        this.$store.state.thoughtRecords[this.selectedRecordIndex].userResponse = this.userResponse;
        
        // ‰øùÂ≠òÂàÜÊûêÁªìÊûú
        this.$store.state.thoughtRecords[this.selectedRecordIndex].analysisResult = this.analysisResult;
        this.$store.saveState();

        // Á¨¨‰∏âÊ≠•ÔºöÁîüÊàêÂàùÂßãÊõø‰ª£ÊÉ≥Ê≥ï
        this.loadingStage = 'ÁîüÊàêÊõø‰ª£ÊÉ≥Ê≥ï';
        this.loadingProgress = 75;
        this.loadingTip = this.getRandomTip();
        await this.generateAlternativeThought();
        
      } catch (error) {
        console.error('ÂàÜÊûêÂ§±Ë¥•:', error);
        this.apiError = error.message || 'ÂàÜÊûêËøáÁ®ã‰∏≠ÂèëÁîüÊú™Áü•ÈîôËØØÔºåËØ∑ÈáçËØï';
      } finally {
        this.loadingProgress = 100;
        this.loadingStage = 'ÂàÜÊûêÂÆåÊàê';
        this.loadingTip = null;
        setTimeout(() => {
        this.isLoading = false;
        }, 500);
      }
    },

    async generateAlternativeThought() {
      if (!this.isLoading) {
      this.isLoading = true;
        this.loadingProgress = 0;
        this.loadingStage = 'ÁîüÊàêÊõø‰ª£ÊÉ≥Ê≥ï';
      }
      
      try {
        this.loadingProgress = 50;
        const response = await this.callLLM(this.getAlternativeThoughtPrompt());
        this.alternativeThought = response.choices[0].message.content;
        this.loadingProgress = 100;
      } catch (error) {
        console.error('ÁîüÊàêÊõø‰ª£ÊÉ≥Ê≥ïÂ§±Ë¥•:', error);
      } finally {
        setTimeout(() => {
        this.isLoading = false;
        }, 500);
      }
    },

    async callLLM(prompt) {
      try {
        // ‰∏çÂÜçÈúÄË¶Å Authorization headerÔºåÁî±‰ª£ÁêÜÂ§ÑÁêÜ
      const headers = {
        'Content-Type': 'application/json'
      };

        // ËØ∑Ê±Ç‰ª£ÁêÜÊúçÂä°ÁöÑÁ´ØÁÇπ
        const proxyEndpoint = '/api/llm-proxy'; 

        // Êï∞ÊçÆÂè™ÂåÖÂê´ prompt
      const data = {
          prompt: prompt
        };

        console.log(`Ê≠£Âú®Ë∞ÉÁî®‰ª£ÁêÜÊúçÂä°: ${proxyEndpoint}`);
        
        // Ë∞ÉÁî®‰ª£ÁêÜÊúçÂä°
        const response = await axios.post(proxyEndpoint, data, { 
          headers,
          timeout: 45000 // ÂèØ‰ª•ÈÄÇÂΩìÂ¢ûÂä†Ë∂ÖÊó∂Êó∂Èó¥ÔºåÂõ†‰∏∫Ê∂âÂèä‰∏§Ê¨°ÁΩëÁªúËØ∑Ê±Ç
        });
        
        return response.data; // ‰ª£ÁêÜ‰ºöÁõ¥Êé•ËøîÂõû LLM API ÁöÑÂéüÂßãÂìçÂ∫îÊï∞ÊçÆÁªìÊûÑ

      } catch (error) {
        console.error('Ë∞ÉÁî®‰ª£ÁêÜÊúçÂä°Â§±Ë¥•:', error);
        if (error.response) {
          console.error('‰ª£ÁêÜÊúçÂä°ÂìçÂ∫î:', error.response.data);
          // Â∞ùËØï‰ªé‰ª£ÁêÜÁöÑÈîôËØØÂìçÂ∫î‰∏≠ÊèêÂèñÊõ¥ÂÖ∑‰ΩìÁöÑ‰ø°ÊÅØ
          const errorMessage = error.response.data?.error || 
                              (error.response.data?.details ? `‰ª£ÁêÜÈîôËØØ: ${error.response.data.details}` : 
                              `‰ª£ÁêÜÊúçÂä°ËøîÂõûÈîôËØØ: ${error.response.status}`);
          throw new Error(errorMessage);
        } else if (error.request) {
          throw new Error('Êú™Êî∂Âà∞‰ª£ÁêÜÊúçÂä°ÂìçÂ∫îÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•ÊàñÁ®çÂêéÈáçËØï');
        } else {
