    parseBiasResponse(response) {
      try {
        const content = response.choices[0].message.content;
        // Â∞ùËØïËß£ÊûêJSONÊ†ºÂºè
        try {
          const parsed = JSON.parse(content);
          if (parsed && parsed.biases) {
            return parsed.biases;
          }
        } catch (jsonError) {
          console.error('Ëß£ÊûêÂÅèÂ∑ÆJSONÂ§±Ë¥•:', jsonError);
          // Â¶ÇÊûúËß£ÊûêÂ§±Ë¥•ÔºåÂ∞ùËØï‰ΩøÁî®Ê≠£ÂàôË°®ËææÂºèÊèêÂèñJSONÈÉ®ÂàÜ
          const jsonMatch = content.match(/\{[\s\S]*\}/);
          if (jsonMatch) {
            try {
              const extractedJson = JSON.parse(jsonMatch[0]);
              if (extractedJson && extractedJson.biases) {
                return extractedJson.biases;
              }
      } catch (e) {
              console.error('ÊèêÂèñJSONÂêéËß£Êûê‰ªçÁÑ∂Â§±Ë¥•:', e);
            }
