        return [];
      } catch (error) {
        console.error('Ëß£ÊûêÂÅèÂ∑ÆÂìçÂ∫îÂ§±Ë¥•:', error);
        return [];
      }
    },

    parseQuestionResponse(response) {
      try {
        const content = response.choices[0].message.content;
        // Â∞ùËØïÁõ¥Êé•Ëß£ÊûêJSON
        try {
          const questions = JSON.parse(content);
          if (Array.isArray(questions)) {
            return questions;
          }
        } catch (jsonError) {
          console.error('Ëß£ÊûêÈóÆÈ¢òJSONÂ§±Ë¥•:', jsonError);
          // Â∞ùËØïÊèêÂèñJSONÊï∞ÁªÑÈÉ®ÂàÜ
          const arrayMatch = content.match(/\[[\s\S]*\]/);
          if (arrayMatch) {
            try {
              const extractedArray = JSON.parse(arrayMatch[0]);
              if (Array.isArray(extractedArray)) {
                return extractedArray;
              }
      } catch (e) {
              console.error('ÊèêÂèñÊï∞ÁªÑÂêéËß£Êûê‰ªçÁÑ∂Â§±Ë¥•:', e);
            }
          }
