        // Â¶ÇÊûúÊó†Ê≥ïËß£Êûê‰∏∫JSONÔºåÂ∞ùËØïÂü∫‰∫éË°åÂàÜÂâ≤
        if (content.includes('\n')) {
          const lines = content.split('\n').filter(line => 
            line.trim() && !line.startsWith('[') && !line.startsWith(']') && !line.startsWith('```')
          );
          if (lines.length > 0) {
            return lines.map(line => line.replace(/^[0-9]+[\.\)]\s*/, '').trim()).filter(q => q);
          }
        }
        
        console.warn('Êó†Ê≥ï‰ªéLLMÂìçÂ∫î‰∏≠Ëß£ÊûêÂá∫ÈóÆÈ¢òÊï∞ÊçÆÔºåÂéüÂßãÂìçÂ∫î:', content);
