import { createApp } from 'vue'
import { createRouter, createWebHistory } from 'vue-router'
import { createI18n } from 'vue-i18n'

// Import global styles
import '../assets/css/main.css';

// Import language files
import scMessages from './locales/sc.json'; // Revert to direct import
import tcMessages from './locales/tc.json'; // Revert to direct import
// import * as scData from './locales/sc.json'; // Remove previous attempt
// import * as tcData from './locales/tc.json'; // Remove previous attempt

import App from '../App.vue'
import ApiConfig from '../components/ApiConfig.vue'
import ThoughtRecord from '../components/ThoughtRecord.vue'
import CognitiveAnalysis from '../components/CognitiveAnalysis.vue'
import Dashboard from '../components/Dashboard.vue'

const router = createRouter({
  history: createWebHistory(),
  routes: [
    { path: '/', component: Dashboard },
    { path: '/config', component: ApiConfig },
    { path: '/record', component: ThoughtRecord },
    { path: '/analysis', component: CognitiveAnalysis }
  ]
})

// Determine initial locale
const getInitialLocale = () => {
  const savedLocale = localStorage.getItem('userLocale');
  // Add more sophisticated detection later if needed (e.g., browser language)
  return savedLocale || 'sc'; // Default to Simplified Chinese
};

// Create i18n instance
const i18n = createI18n({
  legacy: false, // Use Composition API mode
  locale: getInitialLocale(), // Set initial locale
  fallbackLocale: 'sc', // Fallback if translation is missing
  messages: {
    sc: scMessages, // Use direct import variable
    tc: tcMessages  // Use direct import variable
  }
});

const store = {
  state: {
    apiConfig: {
      endpoint: 'https://api.openai.com/v1/chat/completions',
      apiKey: '',
      model: 'gpt-3.5-turbo'
    },
    thoughtRecords: [],
    selectedRecordIndex: undefined,
    currentLocale: getInitialLocale() // Add locale state
  },
  saveState() {
    localStorage.setItem('cbtHelperState', JSON.stringify(this.state));
    localStorage.setItem('userLocale', this.state.currentLocale); // Save locale preference
  },
  restoreState() {
    const saved = localStorage.getItem('cbtHelperState');
    if (saved) {
      try {
        const parsedState = JSON.parse(saved);
        this.state = {
          ...this.state,
          ...parsedState,
          apiConfig: { ...this.state.apiConfig, ...parsedState.apiConfig },
          currentLocale: localStorage.getItem('userLocale') || 'sc' // Restore locale
        };
      } catch (e) {
        console.error('ÊÅ¢Â§çÁä∂ÊÄÅÂ§±Ë¥•:', e);
      }
    }
    // Ensure i18n instance locale matches restored state
    i18n.global.locale.value = this.state.currentLocale;
  },
  setLocale(locale) {
    if (['sc', 'tc'].includes(locale)) {
      this.state.currentLocale = locale;
      i18n.global.locale.value = locale; // Update i18n instance
      this.saveState(); // Save the new locale preference
    } else {
      console.warn(`Unsupported locale: ${locale}`);
    }
  },
  // Ê∏ÖÈô§ÊâÄÊúâËÆ∞ÂΩïÊï∞ÊçÆ
  clearAll() {
    this.state.thoughtRecords = [];
    this.saveState();
  }
}

store.restoreState()

const app = createApp(App)
app.use(router)
app.use(i18n) // Mount i18n

// Provide store globally
app.provide('store', store)

app.mount('#app')