<template>
  <div class="config-container">
    <h2 class="mb-4">üîß API ËÆæÁΩÆ</h2>
    
    <div class="card">
      <div class="form-group">
        <label>API Á´ØÁÇπ</label>
        <input 
          type="text" 
          class="form-control"
          v-model="apiConfig.endpoint"
          placeholder="https://api.openai.com/v1/chat/completions"
        >
      </div>

      <div class="form-group">
        <label>API ÂØÜÈí•</label>
        <input
          type="password"
          class="form-control"
          v-model="apiConfig.apiKey"
          placeholder="sk-xxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx"
        >
      </div>

      <div class="form-group">
        <label>Ê®°ÂûãÈÄâÊã©</label>
        <select class="form-control" v-model="selectedModelOption" @change="handleModelChange">
          <option value="gpt-3.5-turbo">gpt-3.5-turbo</option>
          <option value="gpt-4">gpt-4</option>
          <option value="gpt-4-turbo">gpt-4-turbo</option>
          <option value="custom">Ëá™ÂÆö‰πâ...</option>
        </select>
      </div>
      
      <div class="form-group" v-if="selectedModelOption === 'custom'">
        <label>Ëá™ÂÆö‰πâÊ®°ÂûãÂêçÁß∞</label>
        <input
          type="text"
          class="form-control"
          v-model="customModelName"
          placeholder="ËæìÂÖ•Ëá™ÂÆö‰πâÊ®°ÂûãÂêçÁß∞"
        >
      </div>

      <button 
        class="btn btn-primary"
        @click="saveConfig"
        :disabled="!isFormValid"
      >
        üíæ ‰øùÂ≠òËÆæÁΩÆ
      </button>

      <div class="alert alert-success mt-3" v-if="showSuccess">
        ‚úÖ ËÆæÁΩÆ‰øùÂ≠òÊàêÂäüÔºÅ
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      apiConfig: { ...this.$store.state.apiConfig },
      showSuccess: false,
      selectedModelOption: '',
      customModelName: ''
    };
  },
  created() {
    // ÂàùÂßãÂåñÊó∂ËÆæÁΩÆselectedModelOption
    const predefinedModels = ['gpt-3.5-turbo', 'gpt-4', 'gpt-4-turbo'];
    if (predefinedModels.includes(this.apiConfig.model)) {
      this.selectedModelOption = this.apiConfig.model;
    } else {
      this.selectedModelOption = 'custom';
      this.customModelName = this.apiConfig.model;
    }
  },
  computed: {
    isFormValid() {
      const hasRequiredFields = this.apiConfig.endpoint && this.apiConfig.apiKey;
      
      if (this.selectedModelOption === 'custom') {
        return hasRequiredFields && this.customModelName;
      } else {
        return hasRequiredFields && this.selectedModelOption;
      }
    }
  },
  methods: {
    handleModelChange() {
      if (this.selectedModelOption !== 'custom') {
        // Â¶ÇÊûúÈÄâÊã©ÁöÑÊòØÈ¢ÑÂÆö‰πâÊ®°ÂûãÔºåÁõ¥Êé•Êõ¥Êñ∞apiConfig.model
        this.apiConfig.model = this.selectedModelOption;
      } else {
        // Â¶ÇÊûúÈÄâÊã©ÁöÑÊòØËá™ÂÆö‰πâÔºåÂ∞ÜcustomModelNameËÆæ‰∏∫ÂΩìÂâçÂÄºÔºàÂ¶ÇÊûú‰∏∫Á©∫Ôºâ
        if (!this.customModelName) {
          this.customModelName = this.apiConfig.model;
        }
      }
    },
    saveConfig() {
      // Â¶ÇÊûúÈÄâÊã©ÁöÑÊòØËá™ÂÆö‰πâÊ®°ÂûãÔºå‰ΩøÁî®customModelName
      if (this.selectedModelOption === 'custom') {
        this.apiConfig.model = this.customModelName;
      }
      
      this.$store.state.apiConfig = this.apiConfig;
      this.$store.saveState();
      this.showSuccess = true;
      setTimeout(() => this.showSuccess = false, 2000);
    }
  }
};
</script>

<style scoped>
.config-container {
  max-width: 600px;
  margin: 2rem auto;
  padding: 0 15px;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  font-weight: 600;
  color: var(--text-primary);
}
</style>