<template>
  <div class="dashboard">
    <header class="welcome-header">
      <h1>{{ t('dashboard.welcome') }}</h1>
      <p class="last-record" v-if="latestRecord">
        {{ t('dashboard.lastRecordPrefix') }}{{ formatDate(latestRecord.timestamp) }}
      </p>
    </header>

    <section class="dashboard-grid stats-section">
      <div class="stat-card total-records">
        <div class="stat-icon">üìä</div>
        <div class="stat-content">
          <h4>{{ t('dashboard.statTotalRecords') }}</h4>
          <div class="stat-number">{{ totalRecordsCount }}</div>
        </div>
      </div>
      
      <div class="stat-card analyzed-records">
        <div class="stat-icon">üß†</div>
        <div class="stat-content">
          <h4>{{ t('dashboard.statAnalyzedRecords') }}</h4>
          <div class="stat-number">{{ analyzedRecordsCount }}</div>
        </div>
      </div>
      
      <div class="stat-card mood-trend">
        <div class="stat-icon">üòä</div>
        <div class="stat-content">
          <h4>{{ t('dashboard.statMoodTrend') }}</h4>
          <div class="trend-indicator" :class="moodTrend.class">
            {{ moodTrend.label }}
          </div>
        </div>
      </div>
    </section>

    <section class="dashboard-grid actions-section">
      <router-link 
        to="/record" 
        class="action-card new-record"
      >
        <div class="action-content">
          <span class="icon">üìù</span>
          <h3>{{ t('dashboard.actionNewRecord') }}</h3>
          <p class="action-desc">{{ t('dashboard.actionNewRecordDesc') }}</p>
        </div>
      </router-link>

      <router-link 
        to="/analysis" 
        class="action-card view-analysis"
        :class="{ disabled: !hasRecords }"
      >
        <div class="action-content">
          <span class="icon">üîç</span>
          <h3>{{ t('dashboard.actionViewAnalysis') }}</h3>
          <p class="action-desc">{{ analysisSummaryText }}</p>
        </div>
      </router-link>
    </section>

    <section class="recent-records" v-if="hasRecords">
      <h2 class="section-title">{{ t('dashboard.recentRecordsTitle') }}</h2>
      <div class="dashboard-grid records-section">
        <div 
          v-for="(record, index) in recentRecords" 
          :key="index"
          class="record-card"
          @click="goToAnalysis(index)"
        >
          <div class="record-header">
            <div class="record-date">{{ formatDate(record.timestamp) }}</div>
            <div class="record-emotions">
              <span 
                v-for="(emotion, idx) in record.emotions.slice(0, 2)" 
                :key="idx"
                class="emotion-tag"
              >
                {{ translateEmotion(emotion) }}
              </span>
              <span v-if="record.emotions.length > 2" class="emotion-more">+{{ record.emotions.length - 2 }}</span>
            </div>
          </div>
          <div class="record-thought">{{ truncateText(record.automaticThought, 80) }}</div>
          <div class="record-status" :class="getRecordStatusClass(record)">
            {{ getRecordStatusLabel(record) }}
          </div>
        </div>
      </div>
    </section>

    <section class="dashboard-grid charts-section">
      <div class="chart-card emotion-stats">
        <div class="chart-card-header">
          <h5><span class="stat-icon-small">üìä</span> {{ t('dashboard.chartEmotionTitle') }}</h5>
          <div class="stat-card-actions" v-if="hasRecords">
            <div class="emotion-count-badge">{{ emotionCountText }}</div>
          </div>
        </div>

        <div class="chart-content emotion-chart" v-if="Object.keys(emotionDistribution).length > 0">
          <div 
            v-for="(count, emotion) in sortedEmotions"
            :key="emotion"
            class="chart-item"
          >
            <div class="chart-label">
              <span class="emotion-emoji">{{ getEmotionEmoji(emotion) }}</span>
              <span class="emotion-name">{{ translateEmotion(emotion) }}</span>
            </div>
            <div class="chart-bar-container">
              <div 
                class="chart-bar"
                :style="{ width: `${(count / maxEmotionCount) * 100}%` }"
              ></div>
              <span class="chart-value">{{ count }}</span>
            </div>
          </div>
        </div>
        
        <div class="no-data" v-else>
          {{ t('dashboard.chartEmotionEmpty') }}
        </div>
      </div>

      <div class="chart-card bias-stats">
        <div class="chart-card-header">
          <h5><span class="stat-icon-small">‚ö†Ô∏è</span> {{ t('dashboard.chartBiasTitle') }}</h5>
          <div class="stat-card-actions" v-if="topBiases.length > 0">
            <div class="bias-count-badge">{{ biasCountText }}</div>
          </div>
        </div>
        
        <div class="chart-content bias-chart" v-if="topBiases.length > 0">
          <div 
            v-for="(bias, index) in topBiases" 
            :key="index"
            class="chart-item"
          >
            <div class="chart-label">
              <span class="bias-icon" :style="{ backgroundColor: getBiasColor(bias.type) }"></span>
              <span class="bias-name">{{ translateBias(bias.type) }}</span>
            </div>
            <div class="chart-bar-container">
              <div 
                class="chart-bar bias-bar"
                :style="{ 
                  width: `${(bias.count / maxBiasCount) * 100}%`,
                  background: `linear-gradient(to right, ${getBiasColorLight(bias.type)}, ${getBiasColor(bias.type)})`
                }"
              ></div>
              <span class="chart-value">{{ bias.count }}</span>
            </div>
          </div>
        </div>
        
        <div class="no-data" v-else>
          {{ t('dashboard.chartBiasEmpty') }}
        </div>
      </div>
    </section>
  </div>
</template>

<script setup>
import { computed, inject } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRouter } from 'vue-router';

const { t } = useI18n();
const store = inject('store');
const router = useRouter();

const totalRecordsCount = computed(() => store.state.thoughtRecords.length);

const latestRecord = computed(() => store.state.thoughtRecords[0]);
const hasRecords = computed(() => totalRecordsCount.value > 0);
const analyzedRecordsCount = computed(() =>
  store.state.thoughtRecords.filter(record => record.alternativeThought).length
);
const recentRecords = computed(() => store.state.thoughtRecords.slice(0, 3));

const analysisSummaryText = computed(() => {
  const count = totalRecordsCount.value;
  return hasRecords.value
    ? t('dashboard.analysisSummary', { count })
    : t('dashboard.analysisPrompt');
});

const moodTrend = computed(() => {
  const records = store.state.thoughtRecords.slice(0, 5);
  if (records.length < 2) {
    return { label: t('dashboard.trendInsufficientData'), class: 'neutral' };
  }
  const recentAvg = records.slice(0, Math.ceil(records.length / 2))
    .reduce((sum, r) => sum + (r.intensity || 50), 0) / Math.ceil(records.length / 2);
  const earlierAvg = records.slice(Math.ceil(records.length / 2))
    .reduce((sum, r) => sum + (r.intensity || 50), 0) / Math.floor(records.length / 2);
  const diff = recentAvg - earlierAvg;
  if (diff < -5) return { label: t('dashboard.trendImproving'), class: 'positive' };
  if (diff > 5) return { label: t('dashboard.trendFluctuating'), class: 'negative' };
  return { label: t('dashboard.trendStable'), class: 'neutral' };
});

const emotionDistribution = computed(() => {
  const emotions = store.state.thoughtRecords
    .flatMap(record => record.emotions || []);
  return emotions.reduce((acc, emotion) => {
    acc[emotion] = (acc[emotion] || 0) + 1;
    return acc;
  }, {});
});
const maxEmotionCount = computed(() => Math.max(...Object.values(emotionDistribution.value || {}), 1));
const sortedEmotions = computed(() => {
  return Object.entries(emotionDistribution.value)
    .sort((a, b) => b[1] - a[1])
    .reduce((obj, [key, value]) => {
      obj[key] = value;
      return obj;
    }, {});
});
const emotionCountText = computed(() => 
  t('dashboard.chartEmotionCount', { count: Object.keys(emotionDistribution.value).length })
);

const topBiases = computed(() => {
  const biases = store.state.thoughtRecords
    .flatMap(record => (record.analysisResult || {}).cognitiveBiases || [])
    .map(bias => bias.type);

  return Object.entries(
    biases.reduce((acc, type) => {
      acc[type] = (acc[type] || 0) + 1;
      return acc;
    }, {})
  )
    .sort((a, b) => b[1] - a[1])
    .slice(0, 5)
    .map(([type, count]) => ({ type, count }));
});
const maxBiasCount = computed(() => {
  return topBiases.value.length > 0
    ? Math.max(...topBiases.value.map(b => b.count), 1)
    : 1;
});
const getBiasesTotal = () => {
  return topBiases.value.reduce((sum, bias) => sum + bias.count, 0);
};
const biasCountText = computed(() => 
  t('dashboard.chartBiasCount', { count: getBiasesTotal() })
);

const formatDate = (isoString) => {
  if (!isoString) return t('dashboard.unknownTime');
  return new Date(isoString).toLocaleDateString('zh-CN', {
    month: 'short',
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit'
  });
};

const truncateText = (text, length) => {
  if (!text) return '';
  return text.length > length ? text.slice(0, length) + '...' : text;
};

const getRecordStatusLabel = (record) => {
  if (record.alternativeThought) {
    return t('dashboard.statusCompleted');
  } else if (record.analysisResult &&
    ((record.analysisResult.cognitiveBiases && record.analysisResult.cognitiveBiases.length > 0) ||
      (record.analysisResult.guidingQuestions && record.analysisResult.guidingQuestions.length > 0))) {
    return t('dashboard.statusInProgress');
  } else {
    return t('dashboard.statusPending');
  }
};

const getRecordStatusClass = (record) => {
  if (record.alternativeThought) {
    return 'status-completed';
  } else if (record.analysisResult &&
    ((record.analysisResult.cognitiveBiases && record.analysisResult.cognitiveBiases.length > 0) ||
      (record.analysisResult.guidingQuestions && record.analysisResult.guidingQuestions.length > 0))) {
    return 'status-progress';
  } else {
    return 'status-pending';
  }
};

const goToAnalysis = (index) => {
  store.selectedRecordIndex = index;
  router.push('/analysis');
};

const getEmotionEmoji = (emotion) => {
  const emojis = {
    'ÁÑ¶Ëôë': 'üò∞', 'Ê≤Æ‰∏ß': 'üòî', 'ÊÑ§ÊÄí': 'üò†', 'ÊÇ≤‰º§': 'üò¢', 'ÁæûÊÑß': 'üò≥',
    'ÂÖ¥Â•ã': 'üòÉ', 'Âπ≥Èùô': 'üòå', 'ÊÅêÊÉß': 'üò®', 'Â§±ËêΩ': 'üòû', 'ÊãÖÂøß': 'üòü',
    'Ëá™Ë¥£': 'üòì', 'Âõ∞ÊÉë': 'ü§î', 'Êó†Âä©': 'ü•∫'
  };
  return emojis[emotion] || 'üò∂';
};
const getBiasColor = (biasType) => {
  const colors = {
    'ÁÅæÈöæÂåñ': '#ff6b6b', 'ÈùûÈªëÂç≥ÁôΩ': '#4ecdc4', 'ËøáÂ∫¶Ê¶ÇÊã¨': '#45b7d1',
    'ÊÉÖÁª™Êé®ÁêÜ': '#96ceb4', 'Â∫îËØ•ÈôàËø∞': '#ffeead', 'ÂøÉÁêÜËøáÊª§': '#ffb347',
    '‰∏™‰∫∫Âåñ': '#c06c84', 'ÊéßÂà∂Ë∞¨ËØØ': '#7579e7', 'Ë¥¥Ê†áÁ≠æ': '#84b1ed', 'ËØªÂøÉÊúØ': '#d183c9'
  };
  return colors[biasType] || '#6c757d';
};
const getBiasColorLight = (biasType) => {
  const color = getBiasColor(biasType);
  return color.replace(')', ', 0.3)').replace('rgb', 'rgba');
};

const emotionTranslationMap = {
  'ÁÑ¶Ëôë': 'common.emotions.anxiety',
  'Ê≤Æ‰∏ß': 'common.emotions.depression',
  'ÊÑ§ÊÄí': 'common.emotions.anger',
  'ÊÇ≤‰º§': 'common.emotions.sadness',
  'ÁæûÊÑß': 'common.emotions.shame',
  'ÂÖ¥Â•ã': 'common.emotions.excitement',
  'Âπ≥Èùô': 'common.emotions.calm',
  'ÊÅêÊÉß': 'common.emotions.fear',
  'Â§±ËêΩ': 'common.emotions.loss',
  'ÊãÖÂøß': 'common.emotions.worry',
  'Ëá™Ë¥£': 'common.emotions.selfBlame',
  'Âõ∞ÊÉë': 'common.emotions.confusion',
  'Êó†Âä©': 'common.emotions.helplessness',
};
const translateEmotion = (emotionText) => {
  const key = emotionTranslationMap[emotionText] || emotionText;
  return key.includes('.') ? t(key) : key;
};

const biasTranslationMap = {
  'ÁÅæÈöæÂåñ': 'common.biases.catastrophizing',
  'ÈùûÈªëÂç≥ÁôΩ': 'common.biases.allOrNothing',
  'ËøáÂ∫¶Ê¶ÇÊã¨': 'common.biases.overgeneralization',
  'ÊÉÖÁª™Êé®ÁêÜ': 'common.biases.emotionalReasoning',
  'Â∫îËØ•ÈôàËø∞': 'common.biases.shouldStatements',
  'ÂøÉÁêÜËøáÊª§': 'common.biases.mentalFilter',
  '‰∏™‰∫∫Âåñ': 'common.biases.personalization',
  'ÊéßÂà∂Ë∞¨ËØØ': 'common.biases.controlFallacy',
  'Ë¥¥Ê†áÁ≠æ': 'common.biases.labeling',
  'ËØªÂøÉÊúØ': 'common.biases.mindReading'
};
const translateBias = (biasText) => {
  const key = biasTranslationMap[biasText] || biasText;
  return key.includes('.') ? t(key) : key;
};
</script>

